import React, {useState} from 'react';
import './Modal.css';
import {reg, Registr} from "./Text";
import {useSelector} from 'react-redux';
import './RegistrationForm.css';
import {useColor} from "../ColorContext";

function RegistrationForm() {
    const [formData, setFormData] = useState({userlastname: '', username: '', Email: '', password: ''});
    const currentLanguage = useSelector((state) => state.language.currentLanguage);
    const handleInputChange = (e) => {
        const {name, value} = e.target;
        setFormData({...formData, [name]: value});
    };
    const { color } = useColor();
    const headerTextStyle = {
        color: color === '#3C3C56' ? 'white' : 'black',
        // другие стили для текста заголовка
    };

    return (
        <div className='formloginreg'>
            <h2>{Registr[currentLanguage].items[0].text}</h2>
            <div className='loginreg'>
                <label htmlFor="userlastname"></label>
                <input
                    type="text"
                    id="userlastname"
                    name="userlastname"
                    value={formData.userlastname}
                    onChange={handleInputChange}
                    placeholder="Фамилия"
                    inputMode="none"

                />
            </div>
            <div className='loginreg'>
                <label htmlFor="username"></label>
                <input
                    type="text"
                    id="username"
                    name="username"
                    value={formData.username}
                    onChange={handleInputChange}
                    placeholder="Имя"
                    inputMode="none"

                />
            </div>
            <div className='loginreg'>
                <label htmlFor="Email"></label>
                <input
                    type="text"
                    id="Email"
                    name="Email"
                    value={formData.Email}
                    onChange={handleInputChange}
                    placeholder="Email"
                    inputMode="none"

                />
            </div>
            <div className='loginreg'>
                <label htmlFor="password"></label>
                <input
                    type="password"
                    id="password"
                    name="password"
                    value={formData.password}
                    onChange={handleInputChange}
                    placeholder="Пароль"
                    inputMode="none"

                />
            </div>
            <div className='helppasswordreg'>
                <div className="forgetreg" style={headerTextStyle}>{reg[currentLanguage].items[0].text}</div>
                <div className="forget" style={headerTextStyle}>{reg[currentLanguage].items[1].text}</div>
            </div>
            <div className='loginregagreements'>
                <input type="checkbox" id="showPassword"/>
                <label htmlFor="showPassword"></label>
                <div className='agreements' style={headerTextStyle}>{reg[currentLanguage].items[2].text}
                </div>
            </div>
            <div className='buttonsubmitreg'>
                <button type="submit">{Registr[currentLanguage].items[1].text}</button>
            </div>

        </div>
    );
}

export default RegistrationForm;
