import React from 'react';
import { useSelector } from 'react-redux';
import {contact} from './Text';
import './Contacts.css';
import Vector1 from '../images/Vector1.svg'
import Vector2 from '../images/Vector2.svg'
import Vector3 from '../images/Vector3.svg'
import Vector4 from '../images/Vector 4.svg'
import logo from "../images/logo.svg";
import logo1 from "../images/logo1.svg";
import {useColor} from "../ColorContext";
function Contacts() {
    const currentLanguage = useSelector((state) => state.language.currentLanguage);
    const { color } = useColor();

    const headerStyle = {
        backgroundColor: color,
        color: color === '#3C3C56' ? 'white' : 'black', // Если фон #3C3C56, то текст белый, иначе черный
        boxShadow: '0px 5px 10px rgba(0, 0, 0, 0.2)',
        zIndex: 1,
        // другие стили заголовка
    };

    return (
        <div className="contacts">
            <h2>{contact[currentLanguage].header}</h2>

            <div className="contact-info">
                <div className="contact-item">
                    <div className='cont'>
                        <img src={color === '#3C3C56' ? Vector2 : Vector3} alt="Logo" />
                        <h4>arman-pv.kz</h4>
                    </div>
                    <div className='cont'>
                        <img src={color === '#3C3C56' ? Vector1 : Vector4} alt="Logo" />
                        <h4>info@arman-pv.kz</h4>
                    </div>
                </div>
            </div>

        </div>
    );
}

export default Contacts;
