import React from 'react';
import {come, Condition} from "./Text";
import { useSelector} from 'react-redux';
import './Conditions.css';
function Conditions() {
    const currentLanguage = useSelector((state) => state.language.currentLanguage);
    return (
        <div className="conditions">
            <div className='instr'>
            <h1 >{Condition[currentLanguage].items[0].header}</h1>
            </div>

                <div className='instr'>
            <span >
                {Condition[currentLanguage].items[0].text}
            </span>
                </div>
            <div className='instr'>
            <span >
                {Condition[currentLanguage].items[1].text}
            </span>
            </div>
            <div className='instr'>
            <span >
                {Condition[currentLanguage].items[2].text}
            </span>
            </div>
            <div className='instr'>
            <h2 >{Condition[currentLanguage].items[3].header}</h2>
            </div>
            <div className='instr'>
            <span >
                {Condition[currentLanguage].items[3].text}
            </span>
            </div>
            <div className='instr'>
            <span >
                {Condition[currentLanguage].items[4].text}
            </span>
            </div>
            <div className='buttonsubmit'>
                <button type="submit">{come[currentLanguage].items[1].text}</button>
            </div>
        </div>
    );
}

export default Conditions;
