import ToggleButton from "./ToggleButton";
import React from "react";
import {useColor} from "../ColorContext";
import './WelcomePage.css';

function Footer() {
    const { color } = useColor();

    const headerStyle = {
        backgroundColor: color,
        color: color === '#3C3C56' ? '#e6f5ff' : 'black', // Если фон #3C3C56, то текст белый, иначе черный
        // другие стили заголовка
    };
    return (
        <div className="Footer">
            <div className='Info-button'  style={headerStyle}>
                <div className='Company-name'>© «Arman-PV baspasy», 2023</div>
                <div className='nav-and-lang'><ToggleButton /></div>
            </div>

        </div>
    );
}

export default Footer;
