import React, { useState } from 'react';
import './Modal.css';
import {come,log} from "./Text";
import {useSelector} from 'react-redux';
import './Login.css';
import {useColor} from "../ColorContext";





function Login() {
    const [formData, setFormData] = useState({ username: '', password: '' });
    const currentLanguage = useSelector((state) => state.language.currentLanguage);
    const [showPassword, setShowPassword] = useState(false);
    const { color } = useColor();
    const headerTextStyle = {
        color: color === '#3C3C56' ? 'white' : 'black',
        // другие стили для текста заголовка
    };
    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData({ ...formData, [name]: value });
    };
    return (
        <div className='formlogin' style={headerTextStyle}>
            <h2>{come[currentLanguage].items[0].text}</h2>


            <div className='login'>
                <label htmlFor="username"></label>
                <input
                    type="text"
                    id="username"
                    name="username"
                    value={formData.username}
                    onChange={handleInputChange}
                    inputMode="none"

                />
            </div>

            <div className='password'>
                <label htmlFor="password"></label>
                <input
                    type={showPassword ? 'text' : 'password'}
                    id="password"
                    name="password"
                    value={formData.password}
                    onChange={handleInputChange}
                    inputMode="none"

                />
            </div>

            <div className='helppassword' >
                <div className='Show'>
                    <input
                        type="checkbox"
                        id="showPassword"
                        checked={showPassword}
                        onChange={() => setShowPassword(!showPassword)}
                        inputMode="none"

                    />
                    <label htmlFor="showPassword" style={headerTextStyle}>{log[currentLanguage].items[0].text}</label>
                </div>
                <div className="forget" style={headerTextStyle}>{log[currentLanguage].items[1].text}</div>
            </div>
                <div className='buttonsubmit'>
                    <button type="submit">{come[currentLanguage].items[1].text}</button>
                </div>

        </div>
    );
}

export default Login;