import './Header.css';
import logo from '../images/logo.svg'
import logo1 from '../images/logo1.svg'
import Instructions from './Instructions';
import Contacts from './Contacts';
import Modal from './Modal'; // Путь к компоненту Modal
import React, { useState } from 'react';
import Conditions from './Сonditions'
import { useDispatch } from 'react-redux';
import { setLanguage } from '../actions';
import { useSelector } from 'react-redux';
import { navig } from './Text';
import { useColor } from '../ColorContext';
import ToggleButton from "./ToggleButton";

function Header() {
    const [isContactsModalOpen, setContactsModalOpen] = useState(false);
    const [isInstructionsModalOpen, setInstructionsModalOpen] = useState(false);
    const [isConditionsModalOpen, setConditionsModalOpen] = useState(false);
    const dispatch = useDispatch();
    const currentLanguage = useSelector((state) => state.language.currentLanguage);
    const [isMobileMenuOpen, setMobileMenuOpen] = useState(false);

    const toggleMobileMenu = () => {
        setMobileMenuOpen(!isMobileMenuOpen);
    };

    const handleLanguageChange = (event) => {
        const selectedLanguage = event.target.value;
        dispatch(setLanguage(selectedLanguage));
    };
    const openContactsModal = () => {
        setContactsModalOpen(true);
    };

    const closeContactsModal = () => {
        setContactsModalOpen(false);
    };
    const openInstructionsModal = () => {
        setInstructionsModalOpen(true);
    };

    const closeInstructionsModal = () => {
        setInstructionsModalOpen(false);
    };

    const openConditionsModal = () => {
        setConditionsModalOpen(true);
    };

    const closeConditionsModal = () => {
        setConditionsModalOpen(false);
    };

    const { color } = useColor();

    const headerStyle = {
        backgroundColor: color === '#3C3C56' ? color : 'white',
        color: color === '#3C3C56' ? 'white' : 'black', // Если фон #3C3C56, то текст белый, иначе черный
        boxShadow: '0px 5px 10px rgba(0, 0, 0, 0.2)',
        zIndex: 1,
        // другие стили заголовка
    };

    return (
        <>
            <div style={headerStyle}>
            <div className="Header" >

                <div className='logoimg'>
                    <a className="logo" href="">
                        <img src={color === '#3C3C56' ? logo : logo1} alt="Logo" />
                    </a>
                </div>
                <div className="mobile-menu-button" onClick={toggleMobileMenu}>
                    &#9776; {/* Иконка гамбургера (Юникод-символ) */}
                </div>
                <div className='togl-mob'>
                    <ToggleButton/>
                </div>
                {isMobileMenuOpen && (
                    <div className="mobile-menu" >
                        <div className="menu-block" style={headerStyle}>
                            <select className='langselect-mob' id='languageSelect' value={currentLanguage} onChange={handleLanguageChange}>
                                <option value='rus'>Русский</option>
                                <option value='kaz'>Қазақша</option>
                            </select>

                            <nav className="top-nav-mob">
                                <div className="desktop-menu-mob">
                                    <ul className='navigcia-mob'>
                                        <li className="navigcia-first-mob" onClick={openContactsModal}>{navig[currentLanguage].items[0].text}</li>
                                        <li className="navigcia-centr-mob" onClick={openInstructionsModal}>{navig[currentLanguage].items[1].text}</li>
                                        <li className="navigcia-last-mob" onClick={openConditionsModal}>{navig[currentLanguage].items[2].text}</li>
                                    </ul>
                                </div>
                            </nav>
                        </div>
                    </div>
                ) }


                <div className="nav-and-lang">
                    <nav className="top-nav">
                        <div className="desktop-menu">
                            <ul className='navigcia'>
                                <li className="navigcia-first" onClick={openContactsModal} >{navig[currentLanguage].items[0].text}</li>
                                <li className="navigcia-centr" onClick={openInstructionsModal} >{navig[currentLanguage].items[1].text}</li>
                                <li className="navigcia-last" onClick={openConditionsModal} >{navig[currentLanguage].items[2].text}</li>
                            </ul>
                        </div>
                    </nav>

                    <select className='langselect' id='languageSelect' value={currentLanguage} onChange={handleLanguageChange}>
                        <option value='rus'>Русский</option>
                        <option value='kaz'>Қазақша</option>
                    </select>
                </div>
            </div>
            <Modal isOpen={isContactsModalOpen} onClose={closeContactsModal}>
                <Contacts />
            </Modal>


            <Modal isOpen={isInstructionsModalOpen} onClose={closeInstructionsModal}>
                <Instructions />
            </Modal>

            <Modal isOpen={isConditionsModalOpen} onClose={closeConditionsModal}>
                <Conditions />
            </Modal>
            </div>
        </>


    );
}

export default Header;
